import React from 'react'

const EventCard = (props) => {
    return (
        <figure className='event'>
            <img src={props.imgUrl} alt=' ' />
            <figcaption>
                <h4> </h4>
                <address> </address>
                <div className="time">
                    <div>31.12.2022</div>
                    <div>15 </div>
                </div>
            </figcaption>
        </figure>
    );
};

export default EventCard