import React from 'react'

const Banner = (props) => {
    const types = [
        {
            id: 0,
            name: ''
        },
        {
            id: 1,
            name: ''
        },
        {
            id: 2,
            name: ''
        }
    ]
    return (
        <figure>
            <img src={props.imgUrl} alt="slide" />
            <figcaption>
                <ul className='top'>
                    <li></li>
                    <li>16+</li>
                </ul>
                <div>
                    <h2> </h2>
                    <ul className='info'>
                        <li> </li>
                    </ul>
                </div>
            </figcaption>
        </figure>
    );
};

export default Banner